import Text from "components/Text";
import React, { ReactElement } from "react";
import infoIcon from "assets/img/common/info.svg";

interface ISectionProps {
  children: ReactElement;
  title?: string;
  alertText?: string;
  renderHeader?: () => React.JSX.Element;
  className?: string;
}

const Section = (props: ISectionProps) => {
  const { title, children, alertText, renderHeader, className } = props;
  return (
    <div className={`bg-white flex flex-col gap-3 p-5 rounded-xl ${className ?? ""}`}>
      {alertText ? (
        <div className="-mt-5 -ml-5 -mr-5 bg-semantic-deepSkyBlue-100 rounded-t-xl px-5 text-xs flex justify-start items-center space-x-2">
          <img src={infoIcon} alt="infoIcon" />
          <Text label={alertText} className="" />
        </div>
      ) : (
        <></>
      )}
      {renderHeader ? (
        renderHeader()
      ) : (
        <Text className="text-xl font-bold text-primary-ptext-1000" label={title} />
      )}
      {children}
    </div>
  );
};

export default Section;
